import React from 'react';

function Skill(props) {
    return (
      <div className="skill">
         <div className="gradient"></div>
         <h2>{props.name}</h2>
         <p>{props.description}</p>
         <ul>{props.list.map(item => (<li>{item}</li>))}</ul>
      </div>
    );
}
  
export default Skill;
  